import LandingPage from './components/LandingPage';
import ThreeD from './components/ThreeD';
import Design from './components/Design';
import Architecture from './components/Architecture';
import About from './components/About';

export const ROUTES = [
  { label: 'home', path: '/', component: LandingPage, props: { exact: true } },
  { label: 'design', path: '/design', component: Design },
  { label: 'arquitetura', path: '/architecture', component: Architecture },
  { label: '3d', path: '/3d', component: ThreeD },
  { label: 'sobre', path: '/sobre', component: About }
];



