import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';

import logo from '../assets/logo.svg';

import {
  Container,
  Row,
  Col,
  NavbarBrand,
  NavbarToggler,
  Navbar,
  Collapse,
  Nav,
  NavItem
} from 'reactstrap';

import { ROUTES } from '../constants';

class Header extends Component {
  state = {
    isNavBarOpen: false
  };

  toggleNavBar = () =>
    this.setState({ isNavBarOpen: !this.state.isNavBarOpen });

  render() {
    return (
      <Container>
        <Row>
          <Col md={12}>
            <Navbar className="navbar-expand-lg navbar-light header">
              <NavbarBrand>
                <img
                  className="img-responsive logo col-md-12"
                  src={logo}
                  alt="Logo"
                />
              </NavbarBrand>
              <NavbarToggler onClick={this.toggleNavBar} />
              <Collapse isOpen={this.state.isNavBarOpen} navbar>
                <Nav className="ml-auto header-navigator" navbar>
                  {ROUTES.map(({ label, path }) => (
                    <NavItem key={label}>
                      <Link to={path} className="nav-link ml-4">
                        {this.props.location.pathname === path
                          ? `[${label}]`
                          : `_${label}`}
                      </Link>
                    </NavItem>
                  ))}
                </Nav>
              </Collapse>
            </Navbar>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default withRouter(Header);



